', 'setleader', 'sellmycar', 'setownablecars', 'setskill', 'test_music', 'unloadveh', 'mpanel', 'quitjob', 'settime', 'right', 'unfreeze', 'giverank', 'settp', 'obj', 'setvip', 'setzarall', 'show_hud', 'skick', 'startpb2', 'slap', 'h', 'sms', 'getadmin', 'sp', 'members', 'nismo', 'specoff', 'ans', 'startns', 'startns1', 'startrecrod', 'stoprecord', 'su', 'drag', 'test_pack', 'tie', 'smoke', 'toduel', 'tp_tcompany', 'tpcord', 'unbanip', 'createpromo', 'uncuff', 'uninvite', 'unloadgun'],
            searchedCommands: [],
            selectedCommand: -1,
            chatInput: '',
            showInput: false,
            chatHistory: [],
            currentHistoryInd: -1,
            messagesHeight: 0,
            smilesShow: false,
            currentSmileTab: 0,
            debug: '',
            testSmile: '',
            cursorPos: 0
        }
    },
    methods: {
        /*
        deleteSmile(i) {
            this.smiles[this.currentSmileTab].splice(i, 1);
            this.debug = JSON.stringify(this.smiles[this.currentSmileTab]);
        },*/
        typeSmile(i) {
            this.testSmile = this.smiles[this.currentSmileTab][i];
            this.$nextTick(() => {
                /*
                let value = this.chatInput;
                value = value.split('');
                const newValue = value.splice(this.cursorPos, 0, this.$refs.smiledebug.innerHTML);
                value = value.join('');
                this.chatInput = value;*/
                this.chatInput += this.$refs.smiledebug.innerHTML;
                this.$refs.chatinput.focus();
                this.cursorPos = value.length;
            });
        },
        onChatInputClick(e) {
            console.log('onChatInputClick')
            this.cursorPos = e.target.selectionStart;
        },
        toggleChat(value = null) {
            if(value !== null) {
                this.showInput = value;
            }
            else this.showInput = !this.showInput;
            cef.set_focus(this.showInput);
            this.$nextTick(() => {
                if(this.showInput) this.$refs.chatinput.focus();
            })
        },
        onButtonClick(btn) {
            this.chatInput = btn[1] === '' ? btn[1] : btn[1] + ' ';
            this.$refs.chatinput.focus();
        },
        compareStrings(str1, str2) {
            const l1 = str1.length, l2 = str2.length;
            const size = l1 > l2 ? l2 : l1;
            let count = 0;
            for (let i = 0; i < size; i++) {
                if (str1[i] === str2[i]) count++;
                else break;
            }
            return (count / size) * 100 > 50;
        },
        scrollChatDown() {
            this.$nextTick(() => {
                document.querySelector(`#message${this.messages.length}`).scrollIntoView();
            });
        },
        onChatInput() {
            this.chatInput = this.chatInput.replace(new RegExp('  ', 'g'), ' ');

            let result = [];
            if (this.chatInput.startsWith('/') && this.chatInput.length >= 2 && this.chatInput.indexOf(' ') === -1) {
                const cmdEntered = this.chatInput.slice(1).split(' ')[0];
                for (let cmd of this.commands) {
                    //console.log(cmdEntered, cmd)
                    if (this.compareStrings(cmdEntered, cmd)) {
                        //console.log(cmd)
                        result.push(cmd);
                    }
                }
            }
            this.selectedCommand = -1;
            this.searchedCommands = result;
        },
        onTab() {

        },
        clearTags(msg) {
            return msg.replace(/<[^>]+>/g, '');
        },
        clearColors(msg) {
            const start = msg.indexOf('{');
            const end = msg.indexOf('}');
            if (end > start) {
                return msg.slice(end);
            }
            return msg;
        },
        onEnter() {
            const text = this.clearTags(this.chatInput);
            if (text.trim() === '') return;

            if(text.trim().startsWith('/pagesize ')) {
                const size = text.trim().split(' ')[1];
                this.setPageSize(parseInt(size));
            }

            this.currentHistoryInd = this.chatHistory.push(text);

            this.chatInput = '';

            //this.addMessageToChat(-1, text);
            if(text.startsWith('/')) cef.emit('cmd', text);
            else cef.emit('chat', text);

            this.toggleChat(false);
        },
        toColor(num) {
            num >>>= 0;
            let b = num & 0xFF,
                g = (num & 0xFF00) >>> 8,
                r = (num & 0xFF0000) >>> 16,
                a = ((num & 0xFF000000) >>> 24) / 255;
            return "rgba(" + [r, g, b, a].join(",") + ")";
        },
        clearChat() {
            this.messages = [];
        },
        addMessageToChat(color, message, type = -1) {
            let messageArray = message.split('{');

            let i = -1;
            for (let token of messageArray) {
                i++;
                let pos = token.search('}');
                if (pos !== -1) {
                    if (pos - i > COLOR_LENGTH) continue;
                    const color = '#' + token.slice(pos - COLOR_LENGTH, pos);
                    messageArray[i] = `<span style="color: ${color}">${token.slice(COLOR_LENGTH + 1)}</span>`;
                }
            }

            this.messages.push({
                id: this.messages.length + 1,
                html: `${messageArray.join('')}</span>`,
                color: this.toColor(color),
                type
            });

            this.scrollChatDown();
        },
        onArrowDown(e) {
            if (this.searchedCommands.length > 0 && this.showInput) {
                if (this.selectedCommand < this.searchedCommands.length - 1) {
                    this.selectedCommand++;
                } else {
                    this.selectedCommand = 0;
                }
                this.chatInput = `/${this.searchedCommands[this.selectedCommand]}`;
            } else {
                if (this.currentHistoryInd < this.chatHistory.length - 1) {
                    this.currentHistoryInd++;
                    this.chatInput = this.chatHistory[this.currentHistoryInd];
                } else {
                    this.currentHistoryInd = 0;
                    this.chatInput = this.chatHistory[this.currentHistoryInd];
                }
            }

            if (this.searchedCommands.length > 0) {
                this.$nextTick(() => {
                    document.querySelector(`#ai${this.selectedCommand}`).scrollIntoView();
                });
            }


            e.returnValue = false;
            e.preventDefault();
            return false;
        },
        onArrowUp(e) {
            if (this.searchedCommands.length > 0 && this.showInput) {
                if (this.selectedCommand > 0) {
                    this.selectedCommand--;
                } else {
                    this.selectedCommand = this.searchedCommands.length - 1;
                }
                this.chatInput = `/${this.searchedCommands[this.selectedCommand]}`;
            } else {
                if (this.currentHistoryInd > 0) {
                    this.currentHistoryInd--;
                    this.chatInput = this.chatHistory[this.currentHistoryInd];
                } else {
                    this.currentHistoryInd = this.chatHistory.length - 1;
                    this.chatInput = this.chatHistory[this.currentHistoryInd];
                }
            }

            if (this.searchedCommands.length > 0) {
                this.$nextTick(() => {
                    document.querySelector(`#ai${this.selectedCommand}`).scrollIntoView();
                });
            }

            e.returnValue = false;
            e.preventDefault();
            return false;
        },
        setPageSize(size) {
            this.pageSize = size;
            this.$refs.messages.style.height = this.messagesHeight = (this.messageHeight * this.pageSize + this.pageSize * this.messageMargin) + 'px'
        }
    },
    mounted() {
        cef.on('chat', (color, text, type) => {
            this.addMessageToChat(color, text, type);
        });

        /*
        this.addMessageToChat(-1, '&#128512;', -1);
        this.addMessageToChat(-1, '–†–µ—Å—Ç–∞—Ä—Ç —á–µ—Ä–µ–∑ 5 –º–∏–Ω—É—Ç', 0);*/

        document.addEventListener('keydown', e => {
            if (e.key === 'F6') {
                this.toggleChat();
            }
            else if(e.key === 'Escape') {
                this.toggleChat(false);
            }
        })

        window.onload = () => {
            //console.log(this.compareStrings('kick', 'k'));
            this.$refs.messages.style.height = this.messagesHeight = (this.messageHeight * this.pageSize + this.pageSize * this.messageMargin) + 'px'
        }
    }
}

